.con{
    background-color: #2874f0;
    width: 100vw;
    height: 6em;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    align-items: center;
    column-gap: 1em;
    position: fixed;
    top: 0;
    left: 0;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.infinite{
    font-size: 40px;
}
.text{
    width: 25em;
    height: 2.5em;
}
.search{
    height:2.5em ;
    width: 5em;
    cursor: pointer;
    background-color: aquamarine;
    border: none;
    border-radius: 4px;
    margin: .2em;
}
.search:hover{
  background-color: rgb(177, 255, 229);

}
.box1{
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.box2{
    display: flex;
    justify-content: center;
    align-items: center;
}
.Acart{
    width: 4em;
    margin-left: .4em;
    
}
.box3{
    display: flex;
    align-items: center;
    justify-content: center;
}
.logo{
    width: 3em;
    margin:1em;
}
.logo1{
  width: 3em;
    margin:1em;
}
.dropdown {
    display: inline-block;
    position: relative;
  }
  .dropdown-content {
    display: none;
    position: absolute;
    width: 100%;
    overflow: auto;
    background-color: antiquewhite;
    box-shadow: 0px 10px 10px 0px rgba(0,0,0,0.4);
  }
  .dropdown:hover .dropdown-content {
    display: block;
  }
  .dropdown-content a {
    display: block;
    color: #000000;
    padding: 5px;
    text-decoration: none;
  }
  .dropdown-content a:hover {
    color: #FFFFFF;
    background-color: #00A4BD;
  }
  .profile{
    width: 3em;
  }
  .btn{
    display: flex;
    align-items: center;
    border: none;
    border-radius: 4px;
    width: 7em;
    justify-content: center;
    
  }
  .main{
    margin-top: 6em;
    height: 100%;
    display: grid;
    gap: 1em;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    justify-content: space-evenly;
    background-color: #f7f0f0;
    
  }
  .box{
   
    /* border: 1px solid black; */
    height: 50em;
    border-radius: 6px;
    margin-top: 1em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .cat{
    /* border: 1px solid tomato; */
    height: 40em;
    width: 35em;
    border-radius: 6px 6px 0px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: white;

  }
  .cat:hover{
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;

  }
  .buy_cart{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border-radius: 0 0 6px 6px;
    width: 35em;
    height: 6em;
    background-color: white;

  }
  .buy{
    cursor: pointer;
    border-radius: 4px;
    padding: .9em;
    background-color: #d3da22;
    border: none;
    width: 40%;
  }
  .buy:hover{
    background-color: #ebf140;
    transition: 1s;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;

    
  }
  .cart{
    border-radius: 4px;
    background-color: #d3da22;
    width: 50%;
    padding: .9em;
    cursor: pointer;
    border: none;
  }
  .cart:hover{
    background-color: #ebf140;
    transition: 1s;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;

  }
  .price{
    color: red;
    margin: 1em;
  }


  @media (max-width : 640px) {
    .main{
      grid-template-columns: 1fr;
    }
    .infinite{
      display: none;
    }
.logo1{
  display: none;
}
.text{
  width: 5em;
}
.search{
  width: 4em;
}
.Acart{
width: 3em;
margin-right: .6em;

} 
  }