        *{
            margin: 0;
            padding: 0;
            box-sizing: border-box;
        }
        html{
            font-size: 62.5%;
        }
        section{
            flex-direction: column;
            display: flex;
            justify-content: center;
            align-items: center;
            min-height: 100vh;
            background-color: #042104;
        }
        .bag{
            width: 15em;
            margin-bottom: 2em;
        }
        .loader { 
            width:20em; 
            margin:0 auto;
            border-radius:10px;
            border:4px solid transparent;
            position:relative;
            padding:1px;
          }
          .loader:before {
            content:'';
            border:1px solid #fff; 
            border-radius:10px;
            position:absolute;
            top:-4px; 
            right:-4px; 
            bottom:-4px; 
            left:-4px;
          }
          .loader .loaderBar { 
            position:absolute;
            border-radius:10px;
            top:0;
            right:100%;
            bottom:0;
            left:0;
            background:#fff; 
            width:0;
            animation:borealisBar 2s linear infinite;
          }
          
          @keyframes borealisBar {
            0% {
              left:0%;
              right:100%;
              width:0%;
            }
            10% {
              left:0%;
              right:75%;
              width:25%;
            }
            90% {
              right:0%;
              left:75%;
              width:25%;
            }
            100% {
              left:100%;
              right:0%;
              width:0%;
            }
          }